import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Sousuo from '../views/sousuo.vue'
import Wode from '../views/wode.vue'
import Pinpaituijian from "@/views/About/pinpaituijian"
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about/',
    name: 'About',
    component: () => import('../views/About.vue'),
    redirect:'/about/pinpaituijian', // 设置默认打开的页面
    children:[
        {
          path:'/about/pinpaituijian',
          component:Pinpaituijian,
          name:Pinpaituijian
        }
    ]
  },
  // {
  //   path: 'http://b2b2c.longbasz.cn/mobile/html/product_first_categroy.html',
  //   name: 'Fenlei',
  //   component: Home
  // },
  {
    path: '/wode',
    name: 'Wode',
    component: Wode
  },
  {
    path: '/sousuo',
    name: 'Sousuo',
    component: Sousuo
  },
]

const router = new VueRouter({
  routes
})

export default router
