    }
  },
  components: {
    HelloWorld
  },
  created(){
    console.log(goodslist)
    this.goods1 = goodslist.goodslist1;
    this.goods2 = goodslist.goodslist2;
    this.goods3 = goodslist.goodslist3;
    console.log(this.titlenav)
    indexdata().then((res)=>{
      console.log(res.data.datas.index[10])
      this.home7 = res.data.datas.index[1].home7
      this.pannerfen = res.data.datas.index[10].show_list.item
      this.goods1 = res.data.datas.index[9].goods.item;
      this.goods2 = res.data.datas.index[12].goods.item;
      this.imgnav1 = res.data.datas.index[8].home2       
