      this.imgtitle = res.data.datas.index[3].home3.item;
      this.imgnavt = res.data.datas.index[5].home2;
      this.jianhuofenghui = res.data.datas.index[6].home1;
      this.jianhuofhlist = res.data.datas.index[7].home2;  //尖货峰惠版块
      // console.log(res.data.datas.index[5].home2.square_image)
      // console.log(res)
      // console.log(this.home7)
    });
    getgg().then((res)=>{
        console.log(res)
        this.gonggao = res.data.datas.article_list
        console.log(this.gonggao)
    })
  },
  //监听滚动事件
		mounted(){
      window.addEventListener('scroll',this.onScroll,false);
      console.log(location.href)
      
      },
      //离开该页面需要移除这个监听的事件，不然会报错;
      destory(){
      window.removeEventListener('scroll',this.onScroll)
		},
		methods:{
      onScroll(){
      //获取页面滚动条scrollTop
      const scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      this.btnClass = scrollTop >150?  'gotop show' : 'gotop'
      },
      scrollToTop(){
      window.scrollTo(0,0);
      }
		}
}
</script>

